import { createRouter, createWebHistory, createWebHashHistory } from 'vue-router';

function load(name) {
    return () => import (`@/${name}.vue`)
}

const component = {
    children: "components/router/View"
};

const routes = [
    {
        path: "/",
        name: "MainLayout",
        component: load("layout/MainLayout"),
        children  : [
            {
                path      : "/",
                name      : "Styleguide",
                component : load("components/guide/guide"),
                meta      : {
                    navigation : "Style Guide",
                },
            },
            {
                path      : "posts",
                component: load(component.children),
                children: [
                    {
                        path      : "",
                        name      : "Posts",
                        component : load("views/posts/List"),
                        meta      : {
                            navigation : "게시글 목록",
                        },
                    },
                    {
                        path      : ":postsIdx",
                        name      : "PostsView",
                        component : load("views/posts/View"),
                        meta      : {
                            navigation : "게시글 보기",
                        },
                    },
                    {
                        path      : "regist",
                        component : load(component.children),
                        children  : [
                            {
                                path      : "",
                                name      : "PostsRegist",
                                component : load("views/posts/Regist"),
                                meta      : {
                                    navigation : "게시글 작성",
                                },
                            },
                            {
                                path      : ":postsIdx",
                                name      : "PostsChange",
                                component : load("views/posts/Regist"),
                                meta      : {
                                    navigation : "게시글 수정",
                                },
                            },
                        ]
                    },
                ]
            },
        ]
    }
]

const router = createRouter({
    history : createWebHistory(),
    routes,
});

export default router;